import React, { useContext, useState, useEffect, useCallback } from 'react'
// components
import FormMessage from '../form/FormMessage'
// context
import { Context } from '../../context/context'

const FormCustomerData = ({ isAutoRegister }) => {
    // context
    const { onFetch, TOKEN_DEFAULT, customerSession } = useContext(Context)

    const inputsDefaultValues = {
        email: '',
        fullname: '',
        phone: '',
        streetAndNumber: '',
        suburb: '',
        cp: '',
        town: '',
        pass: '',
        confirm: ''
    }

    // states
    const [inputs, setInputs] = useState(inputsDefaultValues)
    const [formMessage, setFormMessage] = useState({
        msg: '',
        isSuccess: false
    })

    const loadCustomerData = useCallback(async () => {
        let res = await onFetch(
            `/customer_authorized.php?fn=getCustomer&id=${customerSession['id']}`,
            'GET',
            customerSession['token']
        )
        console.log('res:', res)
        if (res !== null) {
            res = res['customer']
            setInputs({
                email: res['email'],
                fullname: res['fullname'],
                phone: res['phone'],
                streetAndNumber: res['streetAndNumber'],
                suburb: res['suburb'],
                cp: res['cp'],
                town: res['town'],
                pass: '',
                confirm: ''
            })
        }
    }, [onFetch, customerSession])

    useEffect(() => {
        if (customerSession) loadCustomerData() // use-callback for prevent execute infinite

        return () => { }
    }, [loadCustomerData, customerSession])


    function inputChange(e) {
        setInputs({
            ...inputs, // same values
            [e.target.name]: e.target.value // only change property same name
        })
    }

    async function formSubmit(e) {
        e.preventDefault()
        let res
        if (isAutoRegister) {
            res = await onFetch(`/index.php?fn=isExistsCustomer&email=${inputs.email}&id=-1`)
            //console.log(res)
            if (res !== null) {
                if (!res['isExistsCustomer']) {
                    // verify pass and confirm
                    if (inputs.pass === inputs.confirm) {

                        // save new customer
                        res = await onFetch(
                            `/index.php?fn=insertCustomer&isAutoregister=${isAutoRegister}`,
                            'POST',
                            TOKEN_DEFAULT,
                            inputs
                        )
                        //console.log(res)
                        if (res !== null) {
                            setFormMessage({
                                msg: 'El cliente ha sido registrado,\ningresa a tu correo para confirmarlo.',
                                isSuccess: true
                            })
                        }
                    }
                    else {
                        setInputs({
                            ...inputs,
                            pass: '',
                            confirm: ''
                        })

                    }
                }
                else
                    setFormMessage({
                        msg: 'El correo ya se encuentra registrado',
                        isSuccess: false
                    })
            }
        }
        else {
            res = await onFetch(
                '/customer_authorized.php?fn=updateCustomer&id=' + customerSession['id'],
                'PUT',
                customerSession['token'],
                inputs
            )
            if (res !== null) {
                setFormMessage({
                    msg: 'Los datos han sido actualizados',
                    isSuccess: true
                })
            }
        }
    }

    function btnCloseFormMessageClick() {
        setFormMessage({
            msg: '',
            isSuccess: false
        })
    }

    return (
        customerSession &&
        <div className='Card'>
            <form className='Form' onSubmit={formSubmit} style={{ display: formMessage.isSuccess ? 'none' : 'block' }}>
                <label htmlFor='email'>Correo</label>
                <input
                    type='email'
                    name='email'
                    id='email'
                    value={inputs.email}
                    onChange={inputChange}
                    readOnly={!isAutoRegister}
                    autoFocus={isAutoRegister}
                    required={true}
                    style={{
                        backgroundColor: isAutoRegister ? 'white' : 'gainsboro'
                    }}
                />
                <label htmlFor='fullname'>Nombre completo</label>
                <input
                    type='text'
                    name='fullname'
                    id='fullname'
                    value={inputs.fullname}
                    onChange={inputChange}
                    autoFocus={!isAutoRegister}
                    required
                />
                <label htmlFor='phone'>Telfono</label>
                <input
                    type='text'
                    name='phone'
                    id='phone'
                    value={inputs.phone}
                    onChange={inputChange}
                    required
                />
                <label htmlFor='streetAndNumber'>Calle y nmero</label>
                <input
                    type='text'
                    name='streetAndNumber'
                    id='streetAndNumber'
                    value={inputs.streetAndNumber}
                    onChange={inputChange}
                    required
                />
                <div className='Form-input-pair-inline'>
                    <div>
                        <label htmlFor='suburb'>Colonia</label>
                        <input
                            type='text'
                            name='suburb'
                            id='suburb'
                            value={inputs.suburb}
                            onChange={inputChange}
                            required
                        />
                    </div>
                    <div>
                        <label htmlFor='cp'>C.P.</label>
                        <input
                            type='text'
                            name='cp'
                            id='cp'
                            value={inputs.cp}
                            onChange={inputChange}
                            required
                        />
                    </div>
                </div>
                <label htmlFor='town'>Municipio</label>
                <input
                    type='text'
                    name='town'
                    id='town'
                    value={inputs.town}
                    onChange={inputChange}
                    required
                />
                <div className='Form-input-pair-inline' style={{ display: isAutoRegister ? 'flex' : 'none' }}>
                    <div style={{ width: '50%' }}>
                        <label htmlFor='pass'>Contrasea</label>
                        <input
                            type='password'
                            name='pass'
                            id='pass'
                            value={inputs.pass}
                            onChange={inputChange}
                            required={isAutoRegister}
                        />
                    </div>
                    <div style={{ width: '50%' }}>
                        <label htmlFor='confirm'>Confirmar</label>
                        <input
                            type='password'
                            name='confirm'
                            id='confirm'
                            value={inputs.confirm}
                            onChange={inputChange}
                            required={isAutoRegister}
                        />
                    </div>
                </div>
                {
                    !formMessage.isSuccess &&
                    <input
                        type="submit"
                        value="Guardar"
                        style={{
                            backgroundColor: 'black',
                            color: 'white',
                            height: '40px',
                            marginTop: '20px',
                            fontSize: '1rem',
                            cursor: 'pointer'
                        }}
                    />
                }
            </form>
            {
                formMessage.msg.length !== 0 &&
                <FormMessage
                    msg={formMessage.msg}
                    btnCloseClick={formMessage.isSuccess ? null : btnCloseFormMessageClick}
                    isSuccess={formMessage.isSuccess}
                />
            }
        </div>
    )
}

export default FormCustomerData