import React, { useContext, useEffect, useState, useCallback } from 'react'
import { useNavigate } from 'react-router';

// context
import { Context } from '../../context/context';

// css
import './CustomerOrdersList.css'

const CustomerOrdersList = () => {
    // context
    const { onFetch, customerSession } = useContext(Context)

    // states
    const [orders, setOrders] = useState([])
    const [orderSelected, setOrderSelected] = useState(null)
    const [itemsOrder, setItemsOrder] = useState([])
    const [dates, setDates] = useState({
        firstDate: new Date(Date.now() - 1000 * 60 * 60 * 24 * 29).toLocaleDateString('en-CA'), // yyyy-MM-dd minus 30days
        lastDate: new Date().toLocaleDateString('en-CA') // yyyy-MM-dd today
    })

    // navigate
    const navigate = useNavigate()

    const loadItemsOrder = useCallback(async (idOrder) => {
        let res = await onFetch(
            `/customer_authorized.php?fn=getItemsOrder&idOrder=${idOrder}`,
            'GET',
            customerSession['token'],
            )
        //console.log(res)

        if (res !== null) setItemsOrder(res['itemsOrder'])

    }, [onFetch, customerSession])

    const loadOrders = useCallback(async () => {
        let res = await onFetch(
            `/customer_authorized.php?fn=getAllOrders&idCustomer=${customerSession['id']}&firstDate=${dates.firstDate}&lastDate=${dates.lastDate}`,
            'GET',
            customerSession['token']
        )
        //console.log(res);
        if (res !== null) {
            // update ordersList
            res = res['orders'];
            //console.log(res);
            setOrders(res)
            if (res.length > 0) {
                setOrderSelected(res[0])
                loadItemsOrder(res[0]['id'])
            }
        }
    }, [onFetch, customerSession, loadItemsOrder, dates.firstDate, dates.lastDate])

    useEffect(() => {
        //console.log('useEffect OrderList')

        if (customerSession) loadOrders()
        else navigate('/not-found')

        return () => { }
    }, [customerSession, navigate, loadOrders])

    function rowClick(e) {
        //console.log(e.target.parentElement.id);

        // only click over <td>
        if (e.target.tagName === 'TD') {
            // clean selection
            let row = document.getElementsByClassName('active')[0];
            if (row) row.classList.remove('active');

            // get idOrder and select row
            const idOrder = parseInt(e.target.parentElement.id) // text
            const i = orders.findIndex(order => order.id === idOrder) // int == text
            setOrderSelected(orders[i])
            row = document.getElementById(idOrder)
            row.classList.add('active')
            loadItemsOrder(idOrder)
        }
    }

    function dateChange(e) {
        setDates({
            ...dates,
            [e.target.name]: e.target.value
        })
    }

    return (
        customerSession &&
        <>
            <p className='ProductsTitle'>MIS PEDIDOS</p>
            <div className='Wrapper-flex'>
                <div className='OrdersCustomer-wrapper-table'>
                    <table className='OrdersCustomer-table'>
                        <thead>
                            <tr>
                                <td colSpan={2}>
                                    <label htmlFor="firstDate"><code>Desde </code></label>
                                    <input type="date"
                                        name="firstDate"
                                        id="firstDate"
                                        value={dates.firstDate}
                                        onChange={dateChange}
                                        required // mozilla remove clear button
                                    />
                                </td>
                                <td colSpan={2}>
                                    <label htmlFor="lastDate"><code>Hasta </code></label>
                                    <input type="date"
                                        name="lastDate"
                                        id="lastDate"
                                        value={dates.lastDate}
                                        onChange={dateChange}
                                        required // mozilla remove clear button
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th className='OrdersCustomer-col-number'>No</th>
                                <th>Fecha</th>
                                <th className='OrdersCustomer-col-number'>Arts</th>
                                <th className='OrdersCustomer-col-number'>Total</th>
                                <th>Referencia pago</th>
                                <th>Estado</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                orders.map((order, i) =>
                                    <tr className={i === 0 ? 'active' : ''} key={order.id} id={order.id} onClick={rowClick}>
                                        <td className='OrdersCustomer-col-number'>{order.id}</td>
                                        <td>{order.created}</td>
                                        <td className='OrdersCustomer-col-number'>{order.items}</td>
                                        <td className='OrdersCustomer-col-number'>${new Intl.NumberFormat('es-MX', { }).format(order.total)}</td>
                                        <td>{order.tokenPay}</td>
                                        <td>
                                            <code className='OrdersCustomer-state'>{order.isDelivered ? `ENTREGADO\n(${order.dateDelivered})` : 'POR ENTREGAR'}</code>
                                        </td>
                                    </tr>
                                )
                            }
                        </tbody>
                        <tfoot>
                            <tr>
                                <td colSpan={6} style={{textAlign: 'center'}}>{orders.length} {orders.length !== 1 ? 'pedidos encontrados' : 'pedido encontrado'}</td>
                            </tr>
                        </tfoot>
                    </table>
                </div>
                <article className='OrdersCustomer-detail Card'>
                    <table className='OrdersCustomer-table-items'>
                        {
                            orderSelected !== null &&
                            <caption>Orden: <b>{orderSelected.id}</b></caption>
                        }
                        <thead>
                            <tr>
                                <th style={{ width: '5%' }}>Cant</th>
                                <th style={{ width: '55%' }}>Descripcin</th>
                                <th style={{ width: '20%' }}>Precio</th>
                                <th style={{ width: '20%' }}>Importe</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                itemsOrder.map(item =>
                                    <tr key={item.id}>
                                        <td className='OrdersCustomer-col-number'>{item.qty}</td>
                                        <td className='OrdersCustomer-col-text'>{item.articleName}</td>
                                        <td className='OrdersCustomer-col-number'>{item.price}</td>
                                        <td className='OrdersCustomer-col-number'>{item.amount}</td>
                                    </tr>
                                )
                            }
                        </tbody>
                        {
                            orderSelected !== null &&
                            <tfoot className='OrdersCustomer-detail-data'>
                                <tr>
                                    <td colSpan={2}>{orderSelected.items} artculos</td>
                                    <td className='OrdersCustomer-col-number'>Total</td>
                                    <td className='OrdersCustomer-col-number'>${orderSelected.total}</td>
                                </tr>
                            </tfoot>
                        }

                    </table>
                </article>
            </div>
        </>
    )
}

export default CustomerOrdersList