import React, { useContext, useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { AiFillCheckCircle } from 'react-icons/ai';
// context
import {Context } from '../../context/context';
// components
import BtnAccept from '../buttons/BtnAccept';
import ModalDelete from '../modal_delete/ModalDelete';
// icons
import { AiFillDelete, AiFillEdit } from 'react-icons/ai'
// css
import './CategoriesList.css'

const CategoriesList = () => {
    //console.log('>>> Categories load...');

    // context
    const { onFetch, userSession } = useContext(Context);

    // states
    const [categories, setCategories] = useState([]);
    const [stateModalDelete, setStateModalDelete] = useState({
        isShowModal: false,
        id: -1
    });

    const navigate = useNavigate();

    useEffect(() => {
        //console.log('useEffect Categories');

        async function categoriesLoad() {
            // load categories
            const json = await onFetch(
                '/category.php?fn=getAllCategories',
                'GET', 
                userSession['token']
                );
            //console.log(json);

            if (json !== null) setCategories(json['categories'])
        }

        if (userSession) categoriesLoad();
        else navigate('/not-found');

        return () => {
            //console.log('useEffect bye') 
        }

    }, [onFetch, navigate, userSession, stateModalDelete]);

    const btnDeleteClick = (id) => {
        //console.log('id before delete: ', id);

        setStateModalDelete({
            isShowModal: true, // show-Modal
            id: id // only change id
        });
        //console.log('after: ', stateModalDelete);
    }

    const onDelete = async () => {
        //console.log('to delete: ', stateModalDelete['id']);

        const json = await onFetch(
            '/category.php?fn=deleteCategory',
            'DELETE',
            userSession['token'],
            {
                id: stateModalDelete['id']
            }
        );
        //console.log(json);

        // to change stateModalDelete, re-execute useEffect and refresh users
        if (json !== null)
            setStateModalDelete({
                isShowModal: false, // hide-modal 
                id: -1
            });
    }

    const btnCancelClick = () => {
        setStateModalDelete({
            isShowModal: false,
            id: -1
        });
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <div style={{ overflow: 'auto' }}>
                <table className="Table CategoriesList-table">
                    <caption><h2>Categoras</h2></caption>
                    <thead>                        
                        <tr>
                            <th className='CategoriesList-col-description'>Descripcin</th>
                            <th>Disponible</th>
                            <th>
                                <Link to="/category-insert" /*style={{ textDecoration: '' }}*/>
                                    <BtnAccept text='Nueva' style={{ width: '50px' }} />
                                </Link>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            categories.map(category =>
                                <tr key={category.id}>
                                    <td>{category.categoryName}</td>
                                    <td className="Table-cell-center">{category.isAvailable && <AiFillCheckCircle className="Table-icon" />}</td>
                                    <td>
                                        { // only if is admin
                                            userSession.isAdmin &&
                                            <>
                                                {/* <BtnPrimary text='Modificar' onClick={() => navigate(`/category-update/${category.id}`)} /> */}
                                                {/* <BtnDanger text='Eliminar' onClick={() => btnDeleteClick(category.id)} /> */}
                                                <AiFillEdit
                                                    className='CategoriesList-icon-edit'
                                                    onClick={() => navigate(`/category-update/${category.id}`)}
                                                />
                                                <AiFillDelete
                                                    className='CategoriesList-icon-delete'
                                                    onClick={() => btnDeleteClick(category.id)}
                                                />
                                            </>
                                        }
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            {/* <td colSpan={5}>{users.length} usuarios</td> */}
                        </tr>
                    </tfoot>
                </table>
            </div>
            <ModalDelete
                isShowModal={stateModalDelete['isShowModal']}
                subject={'Ests segur@ de eliminar la categora y todos sus productos?'}
                btnDeleteClick={onDelete}
                btnCancelClick={btnCancelClick}
            />
        </div>
    )
}

export default CategoriesList