//------------------ BACKEND --------------------//
//const ENDPOINT = 'http://192.168.0.27/react_php-damaxboutique/backend'; // dev
const API_NO_CORS = 'https://cors-anywhere.herokuapp.com/'
const ENDPOINT = API_NO_CORS + 'http://gucitex.com.mx/web-damaxboutique/backend'; // dist
const TOKEN_DEFAULT = '0eab84ea-917d-4817-9e33-c98caecf626d'; // only for getDataUserFromPass

// ----------------- PAYPAL ------------------//
// sandbox
const PAYPAL_ENDPOINT = 'https://api-m.sandbox.paypal.com' // sandbox
const PAYPAL_CLIENTID = 'ASTGhIaKlycjzaLrjCbuEzROMDBpRfb23NWX4DLbyoLk-Y9D-QTACRisGtWBE2LqqZdckxRJ-GY_86kl'
const PAYPAL_SECRET = 'EC_FmktrxY0UDcOi_GjWB4I4m5MHJdadRxE2y8Yzy-uOd0qX_WowLmxQhl1xzgezRvXKgVLDLPyoGnSH'
// live
//const PAYPAL_ENDPOINT = 'https://api-m.paypal.com' // live


export const Context = createContext()

export const ContextProvider = ({ children }) => {
    console.log('>>>>> CONTEXT LOAD <<<<<')
    //------------------- STATES ----------------------//
    const [userSession, setUserSession] = useState(JSON.parse(window.sessionStorage.getItem('damax')))
    const [customerSession, setCustomerSession] = useState(JSON.parse(window.localStorage.getItem('customer')));
    const [cart, setCart] = useState(JSON.parse(window.localStorage.getItem('cart')) ?? [])

    const [totalCart, setTotalCart] = useState(
        // Calculate from articles cart
        cart.reduce((total, article) => total += article['amount'], 0)
    )
    const [isReturnToCart, setIsReturnToCart] = useState(false)

    //-------------- NAVIGATE -------------//
    const navigate = useNavigate();

    //--------------- FUNCTIONS ---------------//
    const closeSessions = useCallback(() => {
        window.sessionStorage.clear()
        window.localStorage.clear()
        setUserSession(null)
        setCustomerSession(null)
        navigate('/');
    }, [navigate])

    async function proccessPromise(prom) {
        let data = await prom.then(res => res.text())
        .then(txt => {
            console.log('resTxt:', txt)
            return JSON.parse(txt)
        })
        .catch(error => {
            console.log('error fetch:', error)
            alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
            return null
        })

        if (data !== null) {
            if (!data['isAuth']) {
                closeSessions()
                data = null
            }
            else delete data['isAuth']
        }
        return data
    }

    // !import into useCallback for no-reload infinite
    const onFetch = async (params, method = 'GET', token = TOKEN_DEFAULT, body = null) => {
        const prom = fetch(ENDPOINT + params, {
            method: method,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token,
                'origin': ''
            },
            body: body !== null ? JSON.stringify(body) : null
        })       

        return await proccessPromise(prom)
    }    

    return (
        <Context.Provider value={{
            // ---- BACKEND ----
            ENDPOINT,

            // ----- FETCH ------
            onFetch,
            
            // ----- USER-SESSION ---- //
            userSession,
            setUserSession,

            //------ CUSTOMER-SESSION
            customerSession,
            setCustomerSession,
            isReturnToCart,
            setIsReturnToCart,

            //------- both sessions
            closeSessions,

            //------ CART
            totalCart,
            setTotalCart,
            cart,
            setCart,

            //------ PAYPAL
            PAYPAL_ENDPOINT,
            PAYPAL_CLIENTID,
            PAYPAL_SECRET
        }}>
            {children}
        </Context.Provider>
    )
}