import { useNavigate, Link } from 'react-router-dom';
import { useState, useEffect, useContext } from "react";
// context
import { Context } from '../../context/context';
// components
import FormMessage from "../form/FormMessage";

export default function UserInput() {
    // context
    const { onFetch, TOKEN_DEFAULT, userSession, setUserSession, customerSession } = useContext(Context);

    // states
    const [inputsTxt, setInputsTxt] = useState({
        email: '',
        pass: '',
    });
    const [msg, setMsg] = useState('');

    const navigate = useNavigate();

    // only first load
    useEffect(() => {
        //console.log('useEffect UserInput');
        if (userSession || customerSession) navigate('/');
    }, [userSession, customerSession, navigate]);

    const showFormMessage = (msg) => {
        setMsg(msg);
    }

    const formOnSubmit = async (e) => {
        e.preventDefault();

        let res = await onFetch(
            '/user.php?fn=getDataUserFromPass', 
            'POST',
            TOKEN_DEFAULT,
            inputsTxt
            )
        setInputsTxt({
            email: '',
            pass: ''
        })

        if (res !== null) {
            res = res['user']
            
            // email and pass OK
            if (res !== null) {
                if (res['isActive']) {
                    // clean fields useless
                    delete res['isActive']
                    delete res['pass']

                    // clear and save user on sessionStorage
                    window.sessionStorage.clear()
                    window.sessionStorage.setItem('damax', JSON.stringify(res));

                    // update user-on-session-state
                    setUserSession(res);

                    // redirect to root
                    //navigate('/'); // import useNavigate from react-router-dom
                }
                else showFormMessage('El usuario no est activo')
            }
            else showFormMessage('Correo y/o contrasea incorrecta');
        }
    }

    const txtOnChange = (e) => {
        //console.log(e.target.value);        
        setInputsTxt({
            ...inputsTxt,
            [e.target.name]: e.target.value // same name inputs and variables
        });
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        !userSession && !customerSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>INGRESO DE USUARIO AUTORIZADO</h3>
                <form onSubmit={formOnSubmit}>
                    <input
                        type="email"
                        name="email"
                        placeholder='Correo'
                        value={inputsTxt.email}
                        onChange={txtOnChange}
                        required
                    />
                    <input
                        type="password"
                        name="pass"
                        placeholder='Contrasea'
                        value={inputsTxt.pass}
                        onChange={txtOnChange}
                        required
                    />
                    {
                        <Link to="/user-pass-lost">
                            <span>Olvid mi contrasea</span>
                        </Link>
                    }
                    <input type="submit" value="INICIAR SESIN" />
                </form>
                {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />}
            </div>
        </div>
    );
}