import React, { useContext, useState } from 'react'

// components
import FormMessage from '../form/FormMessage'

// context
import { Context } from '../../context/context'

const FormCustomerPass = () => {
  // context
  const { onFetch, customerSession } = useContext(Context)

  const inputsDefaultValues = {
    current: '',
    pass: '',
    confirm: ''
  }

  // states
  const [inputs, setInputs] = useState(inputsDefaultValues)
  const [formMessage, setFormMessage] = useState({
    msg: '',
    isSuccess: false
  })

  function inputChange(e) {
    setInputs({
      ...inputs, // same values
      [e.target.name]: e.target.value // only change property same name
    })
  }

  async function formSubmit(e) {
    e.preventDefault()

    // verify current pass
    let res = await onFetch(
      '/customer_authorized.php?fn=isSamePass',
      'POST',
      customerSession.token,
      { pass: inputs.current }
    )

    if (res !== null) {
      if (res['isSamePass']) {
        if (inputs.pass === inputs.confirm) {
          await onFetch(
            '/customer_authorized.php?fn=changePass',
            'POST',
            customerSession.token,
            { pass: inputs.pass }
          )
          setFormMessage({
            msg: 'La contrasea ha sido actualizada',
            isSuccess: true
          })
        }
        else
          setFormMessage({
            msg: 'Nueva contrasea y confirmacin\nson diferentes',
            isSuccess: false
          })
      }
      else
        setFormMessage({
          msg: 'Contrasea actual incorrecta',
          isSuccess: false
        })
      setInputs(inputsDefaultValues)
    }
  }

  function btnCloseMessageClick() {
    setFormMessage({
      msg: '',
      isSuccess: false
    })
  }

  return (
    <div className='FormCard'>
      {
        !formMessage.isSuccess &&
        <form onSubmit={formSubmit}>
          <input
            type="password"
            name="current"
            value={inputs.current}
            placeholder='Contrasea actual'
            onChange={inputChange}
            required
          />
          <input
            type="password"
            name="pass"
            value={inputs.pass}
            placeholder='Nueva contrasea'
            onChange={inputChange}
            required
          />
          <input
            type="password"
            name="confirm"
            value={inputs.confirm}
            placeholder='Confirmar nueva contrasea'
            onChange={inputChange}
            required
          />
          <input type="submit" value="Aceptar" />
        </form>
      }
      {
        formMessage.msg.length !== 0 &&
        <FormMessage
          msg={formMessage.msg}
          btnCloseClick={formMessage.isSuccess ? null : btnCloseMessageClick}
          isSuccess={formMessage.isSuccess}
        />
      }
    </div>
  )
}

export default FormCustomerPass