import { useNavigate } from 'react-router-dom';
import { useState, useEffect, useContext } from "react";
// context
import { Context } from '../../context/context';
// components
import FormMessage from "../form/FormMessage";

export default function UserChangePass() {

    // context
    const { userSession, onFetch } = useContext(Context);

    const navigate = useNavigate();

    const [inputsTxt, setInputsTxt] = useState({
        current: '',
        pass: '',
        confirm: '',
        isPassChanged: false
    });

    const [msg, setMsg] = useState('');

    // only first load
    useEffect(() => {
        //console.log('useEffect UserChangePass');
        if (!userSession) navigate('/not-found');
    });

    const showFormMessage = (msg) => {
        setMsg(msg);
    }

    const formOnSubmit = async (e) => {
        e.preventDefault();

        // verify current pass
        let res = await onFetch(
            '/user.php?fn=isPassOk',
            'POST',
            userSession.token,
            {
                pass: inputsTxt.current,
                id: userSession.id
            }
        );
        //console.log(res);
        if (res !== null) {
            setInputsTxt({
                ...inputsTxt,
                current: '',
                pass: '',
                confirm: ''
            })
            setMsg('');
            if (res['isPassOk']) {
                // verify if new pass = confirm
                if (inputsTxt.pass === inputsTxt.confirm) {
                    res = await onFetch(
                        '/user.php?fn=setUserPass',
                        'POST',
                        userSession.token,
                        {
                            pass: inputsTxt.pass,
                            id: userSession.id
                        }
                    );
                    //console.log(res);

                    if (res !== null) {
                        if (res['isPassChanged']) {
                            setInputsTxt({
                                ...inputsTxt,
                                isPassChanged: true
                            });
                            setMsg('Contrasea ha sido actualizada');
                        }
                    }
                }
                else showFormMessage('Nueva contrasea y su confirmacin\nson diferentes')
            }
            else showFormMessage('Contrasea actual incorrecta')
        }
    }

    const txtOnChange = (e) => {
        //console.log(e.target.value);        
        setInputsTxt({
            ...inputsTxt, // copy same data
            [e.target.name]: e.target.value // only update input changued
        });
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>CAMBIAR CONTRASEA DE USUARIO</h3>
                { // only if isPassChange = false
                    !inputsTxt.isPassChanged &&
                    <form onSubmit={formOnSubmit}>
                        <input
                            type="password"
                            name="current"
                            placeholder='Contrasea actual'
                            value={inputsTxt.current}
                            onChange={txtOnChange} 
                            required
                        />
                        <input
                            type="password"
                            name="pass"
                            placeholder='Nueva contrasea'
                            value={inputsTxt.pass}
                            onChange={txtOnChange}
                            required
                        />
                        <input
                            type="password"
                            name="confirm"
                            placeholder='Confirmar nueva contrasea'
                            value={inputsTxt.confirm}
                            onChange={txtOnChange}
                            required
                        />
                        <input
                            type="submit"
                            value="CAMBIAR"
                            style={{ backgroundColor: 'dodgerblue', borderRadius: '5px' }}
                        />
                    </form>

                }
                {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} isSuccess={inputsTxt.isPassChanged} />}
            </div>
        </div>
    );
}