import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
// context
import { Context } from '../../context/context'
// components
import FormMessage from '../form/FormMessage'

const CustomerConfirmEmail = () => {
  // context
  const { customerSession, onFetch, TOKEN_DEFAULT } = useContext(Context)
  // params
  const params = useParams()
  // states
  const [idCustomer, setIdCustomer] = useState(-1)
  const [formMessage, setFormMessage] = useState({
    msg: '',
    isSuccess: false
  })

  // navigate
  const navigate = useNavigate()

  useEffect(() => {
    //console.log(params)
    window.history.replaceState(null, '', '/customer-confirm-email') // clean token of url

    async function getIdCustomerFromToken() {
      // get idCustomer from token (-1 not exists); if exists change token
      let res = await onFetch(`/index.php?fn=getIdCustomerFromToken&token=${params['token']}`)
      if (res !== null) {
        //console.log(res)
        setIdCustomer(res['id'])

        // verify if idCustomer > -1
        if (res['id'] > -1) {
          // activate account of customer
          res = await onFetch(
            `/index.php?fn=confirmEmailCustomer`,
            'PUT',
            TOKEN_DEFAULT,
            {
              id: res['id']
            }
          )
          setFormMessage({
            msg: 'Tu correo ha sido confirmado.',
            isSuccess: true
          })
        }
        else {
          setFormMessage({
            msg: 'El link de confirmacin no es vlido.',
            isSuccess: false
          })
        }
      }
    }

    if (!customerSession && params['token']) getIdCustomerFromToken()
    else navigate('/not-found')
  }, [customerSession, params, navigate, onFetch, TOKEN_DEFAULT])

  return (
    !customerSession &&
    <>
      <p className='ProductsTitle'>CONFIRMAR CORREO</p>
      <div className='Wrapper-flex'>
        <div className='Card' style={{ width: '300px' }}>
          {
            formMessage.msg.length > 0 &&
            <>
              <FormMessage
                msg={formMessage.msg}
                isSuccess={formMessage.isSuccess}
              />

              {idCustomer > -1 &&
                <Link to='/input'>
                  <p>Iniciar sesin</p>
                </Link>
              }
            </>
          }
        </div>
      </div>
    </>
  )
}

export default CustomerConfirmEmail