import { useState, useContext, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
// components
import FormMessage from '../form/FormMessage';
// context
import { Context } from '../../context/context'

export default function CustomerInput() {
    // context
    const {
        onFetch,
        TOKEN_DEFAULT,
        userSession,
        customerSession,
        setCustomerSession,
        closeSessions,
        isReturnToCart,
        setIsReturnToCart
    } = useContext(Context)
    // states
    const [txtInputs, setTxtInputs] = useState({
        email: '',
        pass: ''
    })
    const [msg, setMsg] = useState('')

    // navigate
    const navigate = useNavigate()

    useEffect(() => {
        //console.log('useEffect-CustomerInput')

        if (customerSession || userSession) navigate('/')

        return () => {
            //console.log('useEffect-CustomerInput BYE') 
        }
    })

    const txtChange = (e) => {
        //console.log(e.target)
        setTxtInputs({
            ...txtInputs,
            [e.target.name]: e.target.value
        })
    }

    const onSubmit = async (e) => {
        e.preventDefault()
        let res = await onFetch(
            '/customer_authorized.php?fn=getDataCustomerFromPass',
            'POST',
            TOKEN_DEFAULT,
            txtInputs
        )
        setTxtInputs({
            email: '',
            pass: ''
        })

        if (res !== null) {
            res = res['customer']

            // email and pass OK
            if (res !== null) {
                if (res['isActive']) {
                    if (res['isEmailConfirmed']) {
                        // close sessions
                        closeSessions()

                        // delete fields useless
                        delete res['phone']
                        delete res['streetAndNumber']
                        delete res['suburb']
                        delete res['cp']
                        delete res['town']
                        delete res['isEmailConfirmed']
                        delete res['isActive']


                        // save data on local-storage
                        window.localStorage.setItem('customer', JSON.stringify(res))
                        setCustomerSession(res);

                        // verify if comming from cart
                        if (isReturnToCart) {
                            //console.log('isReturnToCart')
                            setIsReturnToCart(false)
                            navigate('/carrito')
                        }
                        else navigate('/')
                    }
                    else setMsg('Tu correo no confirmado,\ningresa a tu correo para confirmarlo.')
                }
                else setMsg('Tu cuenta no est activa.')
            }
            else setMsg('Correo y/o contrasea incorrecta.')
            setTxtInputs({
                ...txtInputs, // same values
                pass: '' // only change 1 property
            })
        }
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        !customerSession && !userSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>INICIA SESIN</h3>
                <form onSubmit={onSubmit}>
                    <input
                        type="email"
                        name="email"
                        value={txtInputs.email}
                        onChange={txtChange}
                        placeholder='Correo'
                        required
                    />
                    <input
                        type="password"
                        name="pass"
                        value={txtInputs.pass}
                        onChange={txtChange}
                        placeholder='Contrasea'
                        required
                    />
                    <Link to="/customer-pass-lost">
                        <span>Olvid mi contrasea</span>
                    </Link>
                    <input type="submit" value="INICIAR SESIN" />
                    <div style={{backgroundColor: 'gainsboro'}}>
                        <code>Cliente prueba:<br/>
                        cliente@damaxboutique.com<br/>
                        contrasea: 1</code>
                    </div>
                </form>
                <small>No tienes una cuenta? <Link to="/customer-register">Crear una cuenta</Link></small>
                {
                    msg !== '' &&
                    <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                }
            </div>
        </div>
    )
}