import { useContext, useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';

// context
import { Context } from '../../context/context'

// css
import './Products.css';

export default function Products() {
    // context
    const { onFetch, ENDPOINT } = useContext(Context)

    // params
    const params = useParams(); // important!
    //console.log(params); // {search: 'categoria'|'texto', txtSearch: <string>}

    const navigate = useNavigate();

    // states
    const [articles, setArticles] = useState([]);
    
    useEffect(() => {
        //console.log('>>> effect Productos')

        async function articlesLoad() {
            let data
            if (params['search'] === 'category')  
                data = await onFetch(`/index.php?fn=getAllArticlesFromCategoryName&categoryName=${params['txtSearch']}&endpoint=${ENDPOINT}`)
            else 
                data = await onFetch(`/index.php?fn=getAllArticlesFromTxt&txtSearch=${params['txtSearch']}&endpoint=${ENDPOINT}`)
            //console.log(data);
            
            if (data !== null) setArticles(data['articles']);                    
        }  

        articlesLoad();

        return () => {
            //console.log('useEffect Productos BYE')
        }

    }, [params, onFetch, ENDPOINT]);

    const imgClick = (id) => {
        //console.log('click to id: ' + id);
        navigate('/product-detail/' + id);
    } 

    return (
        <>            
            <p className='ProductsTitle'>{params['txtSearch']}</p>
            <div className='Grid'>
                {
                    articles.map(article =>
                        article['isAvailable'] &&
                        <div className='Product-card' key={article.id}>
                            {
                                // if without else
                                article.stock === 0 && <span>AGOTADO</span>
                            }
                            <img src={article.nameImg} alt={article.articleName} onClick={() => imgClick(article.id)} width="220" height="250" />
                            <p>{article.articleName}</p>
                            <p className='Product-price'>$ {article.price}</p>
                        </div>
                    )
                }
            </div>
        </>
    );
}