import React, { useCallback, useContext, useEffect/*, useState*/ } from 'react'
import { useNavigate } from 'react-router'
// context
import { Context } from '../../context/context'

const PayPal = () => {
  // context
  const { 
    customerSession, 
    cart, 
    PAYPAL_ENDPOINT, 
    PAYPAL_CLIENTID, 
    PAYPAL_SECRET 
  } = useContext(Context)
  
  // navigate
  const navigate = useNavigate()

  const createOrder = useCallback(async () => {
    // credentials paypal to base64
    const auth = btoa(`${PAYPAL_CLIENTID}:${PAYPAL_SECRET}`)
    
    // generate items of purchase
    const purchaseUnits = cart.map(article => {
      return {
        reference_id: article['id'], 
        amount: {
          currency_code: 'MXN',
          value: article['amount']
        },
        description: article['articleName']
      }
    })

    const order = {
      intent: 'CAPTURE',
      purchase_units: purchaseUnits,
      application_context: {
        brand_name: 'DAMAX BOUTIQUE',
        landing_page: 'LOGIN',
        user_action: 'PAY_NOW',
        return_url: 'http://localhost:3000/cart',
        cancel_url: 'http://localhost:3000/cart'
      }      
    }

    // fetch POST
    let res = null
    await fetch(`${PAYPAL_ENDPOINT}/v2/checkout/orders`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Basic ' + auth
      },
      body: JSON.stringify(order)
    }).then(data => data.text())
    .then(txt => {
      console.log('txt:', txt)
      res = JSON.parse(txt)
    })
    .catch(error => console.error('error:', error))

    console.log('res:', res)
    if (res['status'] && res['status'] === 'CREATED') {
      // redirect to PAYPAL; passCustomer: wo=9[R%z
      //console.log(res['links'][1]['href'])
      window.location.replace(res['links'][1]['href'])      
    }
    else {
      console.log('ERROR AL REALIZAR ORDEN')
    }


  }, [PAYPAL_CLIENTID, PAYPAL_ENDPOINT, PAYPAL_SECRET, cart])
      
  useEffect(() => {
    if (customerSession) createOrder()
    else navigate('not-found')
    
    return () => {}
  }, [customerSession, createOrder, navigate])

  return (
      customerSession && 
      <div className="Wrapper-flex">
          <div>Generando orden PayPal...</div>
      </div>
  )
}

export default PayPal