import React, { useEffect, useState, useContext } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
// context
import { Context } from '../../context/context';
// icon
import { AiOutlineArrowLeft, AiOutlinePlus, AiOutlineMinus } from 'react-icons/ai';
// components
import FormMessage from '../form/FormMessage';
// css
import './ProductDetail.css';

const ProductDetail = () => {
  // context 
  const context = useContext(Context);
  
  // params
  const params = useParams();
  // navigate
  const navigate = useNavigate();
  // states
  const [article, setArticle] = useState(null);
  const [qty, setQty] = useState(1);
  const [formMessage, setFormMessage] = useState({
    msg: '',
    isSuccess: false
  }) 

  useEffect(() => {
    //console.log('>>> useEffect ProductDetail');

    async function getArticle() {
      const data = await context.onFetch(`/index.php?fn=getArticle&id=${params['id']}&endpoint=${context.ENDPOINT}`);
      if (data !== null) setArticle(data['article'])
    }

    if (article === null) getArticle()

    window.localStorage.setItem('cart', JSON.stringify(context.cart))

    return () => { }
  }, [article, params, context, context.buttonENDPOINT]);

  const backClick = () => {
    // page previous: products|cart
    navigate(-1)
  }

  const addClick = async () => {
    onCloseFormMessage()

    // verify if exists 1 more
    let res = await context.onFetch(
      `/customer_authorized.php?fn=getStockArticle&id=${article['id']}`)
    if (res !== null) {
      res = res['stock']
      if (res >= qty + 1) setQty(qty + 1)
      else
        setFormMessage({
          msg: `Solo restan ${res}`,
          isSuccess: false
        })
    }
  }

  const substractClick = () => {
    onCloseFormMessage()
    if (qty >= 2) setQty(qty - 1);
  }

  const btnAddToCartClick = async () => {
    onCloseFormMessage()

    // verify if exists stock
    let res = await context.onFetch(`/customer_authorized.php?fn=getStockArticle&id=${article['id']}`)
    if (res !== null) {
      res = res['stock']
      // update stock of article
      article['stock'] = res

      if (res >= qty) {
        // take index of articles with id equals
        const i = context.cart.findIndex(articleCart => articleCart.id === article.id)
               
        // add new article to cart
        if (i === -1) {
          // new properties
          article['qty'] = qty;
          article['amount'] = qty * article['price']
          
          // clean fields useless
          delete article['categoryName']
          delete article['idCategory']
          delete article['description']
          delete article['isAvailable']
          
          context.setCart([...context.cart, article])
        }
        // only change qty - amount
        else {
          // change qty - amount of article[index]
          const newCart = context.cart.map((article, index) => {
            if (index === i) {
              article['qty'] += qty
              article['amount'] = qty * article['price']
            }
            return article
          })
          context.setCart(newCart)
        }
 
        setFormMessage({
          msg: 'El artculo ha sido agregado al carrito',
          isSuccess: true
        })
      }
      else
        setFormMessage({
          msg: `Solo restan ${res}.`,
          isSuccess: false
        })
    }
  }

  const onCloseFormMessage = () => {
    setFormMessage({
      msg: '',
      isSuccess: false
    })
  }

  return (
    article &&
    <>
      {/* {console.log('cart render:', context.cart)} */}
      {/* ------------------- BACK BAR --------------------- */}
      <div className='Detail-top'>
        <AiOutlineArrowLeft className='Detail-icon' onClick={backClick} />
      </div>

      <div className='Detail-wrapper'>

        {/* ------------------- CARD IMAGE --------------------- */}
        <div className='Card Detail-card-image'>
          <img src={article['nameImg']} alt={article['articleName']} />
        </div>

        {/* -------------- DATA CARD -------------- */}
        <div className='Card Detail-card-data'>
          <div className='Detail-card-data-text'>
            <h4>DAMAX BOUTIQUE</h4>
            <p className='Detail-title'>{article['articleName']}</p>
            <p className='Detail-price'>$ {article['price']}</p>

            {/* qty */}
            <div className='Detail-qty'>
              <AiOutlineMinus className='Detail-icon' onClick={substractClick} />
              <span>{qty}</span>
              <AiOutlinePlus className='Detail-icon' onClick={addClick} />
            </div>

            <div className='Detail-stock'>
              <span>{article['stock'] === 0 ? 'AGOTADO' : article['stock'] <= 12 ? 'SOLO RESTAN: ' + article['stock'] : 'DISPONIBLE'}</span>
            </div>
            <p className='Detail-title'>Descripcin</p>
            <pre className='Detail-description'>{article['description']}</pre>
          </div>
          <div className='Detail-card-data-buttons'>
            { // only if not user on session and stock >= qty
              article['stock'] >= qty && !formMessage.isSuccess &&
              <button
                className='Detail-btn-cart'
                onClick={btnAddToCartClick}
              >AGREGAR AL CARRITO</button>
            }
            {
              formMessage.msg !== '' &&
              <FormMessage
                msg={formMessage.msg}
                btnCloseClick={formMessage.isSuccess ? null : onCloseFormMessage}
                isSuccess={formMessage.isSuccess} />
            }
          </div>
        </div>
      </div>
    </>
  )
}

export default ProductDetail