import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
// context
import { Context } from '../../context/context'
// components
import FormMessage from './FormMessage'

const CustomerPassRestore = ({ isAdmin }) => {
    // context
    const { customerSession, onFetch, TOKEN_DEFAULT } = useContext(Context)
    // params
    const params = useParams()
    // states
    const [id, setId] = useState(-1)
    const [inputs, setInputs] = useState({
        pass: '',
        confirm: ''
    })
    const [formMessage, setFormMessage] = useState({
        msg: '',
        isSuccess: false
    })
    // navigate
    const navigate = useNavigate()

    useEffect(() => {
        //console.log(params)
        const url = isAdmin ? '/user-pass-restore' : '/customer-pass-restore' 
        window.history.replaceState(null, '', url)

        async function getIdCustomerFromToken() {
            // get id from token (-1 not exists)
            let res
            if (isAdmin) res = await onFetch(`/index.php?fn=getIdUserFromToken&token=${params['token']}`) 
            else res = await onFetch(`/index.php?fn=getIdCustomerFromToken&token=${params['token']}`)
            //console.log(res) // null|{idCustomer: <int>}
            if (res !== null) {
                setId(res['id'])

                // verify if id > -1
                if (res['id'] > -1) {
                    // hide message
                    setFormMessage({
                        msg: '',
                        isSuccess: false
                    })
                }
                else {
                    // show message
                    setFormMessage({
                        msg: 'El link para restaurar tu contrasea\nno es vlido.',
                        isSuccess: false
                    })                    
                }
            }
        }

        if (!customerSession && params['token']) getIdCustomerFromToken()
        else navigate('/not-found')
    }, [customerSession, params, navigate, onFetch, isAdmin])

    function inputChange(e) {
        setInputs({
            ...inputs, // same values
            [e.target.name]: e.target.value // only change 1 value named equals that e.target.name 
        })
    }

    async function onSubmit(e) {
        e.preventDefault()

        // verify if pass and confirm are equals
        if (inputs.pass === inputs.confirm) {
            // update new pass
            let res 
            if (isAdmin) {
                res = await onFetch(
                    '/index.php?fn=changePassUser', 
                    'PUT',
                    TOKEN_DEFAULT,
                    {
                        id: id, 
                        pass: inputs.pass
                    }
                )
            }
            else {
                res = await onFetch(
                    '/index.php?fn=changePassCustomer', 
                    'PUT',
                    TOKEN_DEFAULT,
                    {
                        id: id, 
                        pass: inputs.pass
                    }
                )
            }
            //console.log(res) // null|{}
            if (res !== null) {
                setInputs({
                    pass: '',
                    confirm: ''
                })
                setFormMessage({
                    msg: 'Tu contrasea ha sido restaurada.',
                    isSuccess: true
                })
            }
        }
        else {
            setInputs({
                pass: '',
                confirm: ''
            })
            setFormMessage({
                msg: 'Las contraseas son diferentes.',
                isSuccess: false
            })
        }

    }

    return (
        !customerSession && params['token'] &&
        <>
            <p className='ProductsTitle'>RESTAURAR CONTRASEA</p>
            <div className='Wrapper-flex'>
                <div className='FormCard'>
                    {
                        id > -1 && !formMessage.isSuccess &&
                        <form onSubmit={onSubmit}>
                            <input
                                type='password'
                                name='pass'
                                placeholder='Nueva contrasea'
                                value={inputs.pass}
                                onChange={inputChange}
                                required
                            />
                            <input
                                type='password'
                                name='confirm'
                                placeholder='Confirmar nueva contrasea'
                                value={inputs.confirm}
                                onChange={inputChange}
                                required
                            />
                            <input
                                type='submit'
                                value='Aceptar'
                            />
                        </form>
                    }
                    {
                        formMessage.msg.length > 0 &&
                        <FormMessage msg={formMessage.msg} isSuccess={formMessage.isSuccess} />
                    }
                    {
                        formMessage.isSuccess &&
                        <Link to={isAdmin ? '/admin' : '/input'}>
                            <p>Iniciar sesin</p>
                        </Link>
                    } 
                </div>
            </div>
        </>
    )
}

export default CustomerPassRestore