import React, { useCallback, useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router';
import { Link, useSearchParams } from 'react-router-dom'
// context
import { Context } from '../../context/context';
// icon
import { AiFillDelete, AiOutlineMinus, AiOutlinePlus } from 'react-icons/ai';
// components
import FormMessage from '../form/FormMessage';
// css
import './Cart.css';

const Cart = () => {
  // context
  const {
    onFetch,
    cart,
    setCart,
    totalCart,
    setTotalCart,
    customerSession,
    setIsReturnToCart
  } = useContext(Context);

  // useNavigate
  const navigate = useNavigate()

  // states
  const [messageTable, setMessageTable] = useState('')
  const [messageSummary, setMessageSummary] = useState({
    msg: '',
    isSuccess: false
  })
  // params after of ?
  const [params] = useSearchParams()

  const insertOrderFromCart = useCallback(async (tokenPay, tokenPayer) => {
    if (cart.length > 0) {
      // create new order on backend
      const body = {
        items: cart.length,
        total: totalCart,
        tokenPay: tokenPay,
        tokenPayer: tokenPayer,
        cart: cart
      }
      const res = await onFetch(
        `/customer_authorized.php?fn=insertOrderFromCart`,
        'POST',
        customerSession['token'],
        body
      )
      //console.log('res:', res)
      if (res !== null) {
        // clean cart
        setCart([]) // to change, useEffect below renew localStorage
        setMessageSummary({
          msg: `El pedido ha sido enviado con el folio: ${res['idOrder']}. GRACIAS POR SU COMPRA.`,
          isSuccess: true
        })
      }
    }
  }, [cart, customerSession, onFetch, totalCart, setCart])

  useEffect(() => {
    //console.log('Effect Cart')

    // is payed
    if (params.has('token') && params.has('PayerID')) insertOrderFromCart(params.get('token'), params.get('PayerID'))

    // save cart on storage
    window.localStorage.setItem('cart', JSON.stringify(cart))

    // update total
    const newTotal = cart.reduce((total, article) =>
      total += article['qty'] * article['price'], 0) // this zero its initial
    setTotalCart(newTotal)

    return () => { }
  }, [cart, params, setTotalCart, insertOrderFromCart]);

  const substractClick = (id) => {
    closeMessageTableClick()

    // find index of article in cart
    const i = cart.findIndex(article => article.id === id)
    //console.log('i: ', i)

    // if exists
    if (i > -1) {
      // if current qty >= 2
      if (cart[i].qty >= 2) {
        const updateCart = cart.map(articleCart =>
          articleCart === cart[i] // same article?
            ? {
              ...articleCart,
              qty: articleCart['qty'] - 1,
              amount: (articleCart['qty'] - 1) * articleCart['price']
            } // copy article, only change qty
            : articleCart // if not exists, return same object
        )
        setCart(updateCart)
      }
    }
  }

  const addClick = async (id) => {
    // hide message table
    closeMessageTableClick()

    // find index of article in cart
    const i = cart.findIndex(article => article.id === id)
    //console.log('i: ', i)

    if (i > -1) {
      // verify stock
      let res = await onFetch(
        `/customer_authorized.php?fn=getStockArticle&id=${id}`)
      if (res !== null) {
        res = res['stock']
        if (res >= cart[i].qty + 1) {
          const updateCart = cart.map(articleCart =>
            articleCart === cart[i] // same article?
              ? {
                ...articleCart,
                qty: articleCart['qty'] + 1,
                amount: (articleCart['qty'] + 1) * articleCart['price']
              } // copy article, only change qty          
              : articleCart // same object
          )
          setCart(updateCart)
        }
        else
          setMessageTable(`${cart[i]['articleName']}: solo restan ${res}`)
      }
    }
  }

  const deleteClick = (id) => {
    //console.log('deleteClick: ' + id);
    setCart(cart.filter(articleCart => articleCart.id !== id));
  }

  //---------------- SUMMARY -----------------//
  function cleanCartClick() {
    setCart([])
    setTotalCart(0)
  }

  function btnStartSessionClick() {
    setIsReturnToCart(true)
    navigate('/input')
  }

  /*
  async function btnOrderClick() {
    // verify if complete qty on database
    let isContinue = true
    cart.forEach(async (articleCart, i) => {
      //console.log(articleCart, i)
      let res = await onFetch(`/customer_authorized.php?fn=isStockComplete&id=${articleCart.id}&qty=${articleCart.qty}`, customerSession.token)
      //console.log(res); // {isAuth: true|false, isStockComplete: true|false}
      if (res !== null) {
        if (!res['isStockComplete']) {
          isContinue = false
          articleWithoutStock = articleCart.articleName
        }

        // last item
        if (i === cart.length - 1) {
          if (isContinue) {
            setMsg('')
            //console.log('save order!!')

            // insert order from cart
            res = await onFetch(
              `/customer_authorized.php?fn=insertOrderFromCart&items=${cart.length}&total=${totalCart}`,
              'POST',
              customerSession.token,
              cart,
            )
            //console.log(res) // {isAuth: true|false, idOrder: <int>}

            setIdOrder(res['idOrder']);

            // clear cart
            setCart([]);
          }
          else {
            setMsg(`Existencia insuficiente:\n${articleWithoutStock}`)
          }
        }
      }
    });
  }
  */

  async function getIsStockComplete() {
    closeMessageSummaryClick()

    // get stock articles
    const newCart = await Promise.all(cart.map(async (article) => {
      let res = await onFetch(
        `/customer_authorized.php?fn=getStockArticle&id=${article['id']}`,
        'GET',
        customerSession['token']
      )
      //console.log('res:', res)
      if (res !== null) {
        article['stock'] = res['stock']
        return article
      }
    }))
    //console.log('newCart:', newCart)

    // verify if stock < qty
    let articleNotStock = newCart.find(article => article['stock'] < article['qty'])
    //console.log(articleNotStock);

    if (articleNotStock !== undefined) {
      setMessageSummary({
        msg: `${articleNotStock['articleName']}: solo restan ${articleNotStock['stock']}`,
        isSuccess: false
      })
    }

    return articleNotStock === undefined
  }

  async function btnPayClick() {
    // verify exists
    if (await getIsStockComplete()) navigate('/paypal')
  }

  function closeMessageTableClick() {
    setMessageTable('')
  }

  function closeMessageSummaryClick() {
    setMessageSummary({
      msg: '',
      isSuccess: false
    })
  }

  return (
    <>
      <div className='Cart-title'>Carrito</div>

      <div className='Wrapper-flex'>
        <div className='Cart-table-wrapper'>
          <table className='Cart-table'>
            <thead>
              <tr className='Cart-row'>
                <th className='Cart-col-product'>Producto</th>
                <th className='Cart-col-qty'>Cantidad</th>
                <th className='Cart-col-price'>Precio</th>
                <th className='Cart-col-amount'>Importe</th>
                <th className='Cart-col-icon'></th>
              </tr>
            </thead>
            <tbody>
              {
                cart.map((articleCart, i) =>
                  <tr className='Cart-row no-title' key={i}>
                    <td className='Cart-col-product'>
                      <div className='Cart-container-img'>
                        <img src={articleCart.imgPath} alt={articleCart.articleName} />
                      </div>
                      <Link to={'/product-detail/' + articleCart.id} className='Cart-description'>
                        {articleCart.articleName}
                      </Link>
                    </td>
                    <td className='Cart-col-qty'>
                      <div className='Cart-qty'>
                        <AiOutlineMinus className='Cart-icon' onClick={() => substractClick(cart[i].id)} />
                        <span>{articleCart.qty}</span>
                        <AiOutlinePlus className='Cart-icon' onClick={() => addClick(cart[i].id)} />
                      </div>
                    </td>
                    <td className='Cart-col-price'>${new Intl.NumberFormat('es-MX').format(articleCart.price)}</td>
                    <td className='Cart-col-amount'>
                      ${new Intl.NumberFormat('es-MX').format(articleCart.qty * articleCart.price)}
                    </td>
                    <td className='Cart-col-icon'>
                      <AiFillDelete className='Cart-icon Cart-icon-delete' onClick={() => deleteClick(cart[i].id)} />
                    </td>
                  </tr>
                )
              }
            </tbody>
            <tfoot>
              <tr>
                <td>
                  {
                    messageTable.length > 0 &&
                    <FormMessage
                      msg={messageTable}
                      btnCloseClick={closeMessageTableClick}
                    />
                  }
                </td>
              </tr>
            </tfoot>
          </table>
        </div>

        {/* ---------------- SUMMARY CARD ------------------- */}
        <div className='Cart-summary'>
          <div className='Card'>
            <h3>RESUMEN</h3>
            <br />
            {
              !messageSummary.isSuccess &&
              <>
                <code>{cart.length} {cart === 1 ? 'artculo' : 'artculos'}</code>
                <h4>Total ${new Intl.NumberFormat('es-MX', { minimumFractionDigits: 2 }).format(totalCart)}</h4>
                {
                  cart.length > 0 &&
                  <>
                    {customerSession
                      ? <button className='Cart-btn-order' onClick={btnPayClick}>PAGAR<br />cliente prueba: customer@damaxboutique.com<br />contrasea prueba: wo=9[R%z)</button>
                      // ? <button className='Cart-btn-order' onClick={btnOrderClick}>ORDENAR</button>
                      : <button className='Cart-btn-order' onClick={btnStartSessionClick}>INICIAR SESIN PARA ORDENAR</button>
                    }
                    <br />
                    <br />
                    <p className='Cart-clean-cart' onClick={cleanCartClick}>Limpiar carrito</p>
                  </>
                }
              </>
            }
            {
              messageSummary.msg.length > 0 &&
              <FormMessage
                msg={messageSummary.msg}
                btnCloseClick={closeMessageSummaryClick}
                isSuccess={messageSummary.isSuccess}
              />
            }
          </div>
        </div>
      </div>
    </>
  )
}

export default Cart